import styled from 'styled-components'

const Page = styled.div`
  background: white;
  box-shadow: 0 0 10px darkgray;
  margin: 0 auto;
  width: 700px;
  height: 700px;
  position: relative;
  overflow: hidden;
`

Page.displayName = 'Page'

export default Page


